/* Header */
.header-ul {

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
  padding: 20px;

}

.header-ul-li-mergesort {
  padding-left: 10px;
  margin-right: auto
}

.header-ul-container-algo {
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.header-ul-li-sortingspeed {
  display: flex;
  justify-content: space-between;

}

.header-ul-li-sortingspeed-h1 {
  margin-right: 20px;
  color: orange;

}

.header-ul-li-arraysize {
  display: flex;
  justify-content: space-between;
}

.header-ul-li-arraysize-h1 {
  margin-right: 20px;
  color: orange;
}

.header-ul-li-algo-h1 {
  margin-right: 20px;
  color: orange;

}

.header-ul-li-mergesort-button {
  background-color: purple;
  border: none;
  color: white;
  cursor: pointer;
}


.header-ul-li-quicksort-button {
  background-color: purple;
  border: none;
  color: white;
  cursor: pointer
}


.header-ul-li-selectionsort-button {
  background-color: purple;
  border: none;
  color: white;
  cursor: pointer
}

.header-ul-li-insertionsort-button {
  background-color: purple;
  border: none;
  color: white;
  cursor: pointer
}

/* Header  End */

.app-button-reset {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #4CAF50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;

}

.app-button-reset:hover {
  background-color: #388e3c;
}

.app-button-sort {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #4CAF50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;

}

.app-button-sort:hover {
  background-color: #388e3c;
}